
body {
	overflow: hidden;
	background-color: #eff0f3;
}

.header {
	width:100%;
	background-color: #f3d2c1;
	color: #001858;
    margin: 0; 
    padding: 0;
    top: 0;
    left: 0;
}

.title {
	font-family: 'Montserrat', sans-serif;
	font-size: 3em;
	margin-bottom: 0px;
	margin-top:0px;
	text-decoration: underline;
}

.subtittle {
	font-family: 'Hind', sans-serif;
	font-size: 1em;
	margin-top: 0px;
}


.button {
	background-color: #ff8e3c;
	color: #0d0d0d;
	margin-left: 15px;
	margin-right: 15px;
	margin-top: 15px;
}

.inputFields {
	margin-left:5px;
	margin-right: 5px;
}

.item {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	column-gap: 10px;
}

.formButton {
	background-color: #ff8e3c;
	color: #0d0d0d;
	margin-right: 15px;
	margin-left: 10px;
}

.down {
	min-width: 0px;
}








